/**
 * 
 * books_reducer : which containe all reducers related to book
 *  
 */
export default function(state={},action){
    switch(action.type){
        case 'GET_BOOKS':
            return { ...state,list:action.payload }
        case 'GET_BOOK':
            return{...state,book:action.payload}
        case 'GET_BOOK_W_REVIEWER':
            return {
                ...state,
                book : action.payload.book,
                reviewer : action.payload.reviewer
            }
        case 'CLEAR_BOOK_W_REVIEWER':
        return{
            ...state,
            book : action.payload.book,
            reviewer : action.payload.reviewer
        }
        case 'ADD_BOOK':
        return{
            ...state, bookImage : action.payload
        }
        case 'ADD_BOOK_START' : 
        return{
            ...state, bookImage : action.payload
        }
        case 'ADD_BOOK_ERROR' : 
        return{
            ...state, bookImage : action.payload
        }
        case 'ADD_BOOK_SUCCESS' : 
        return{
            ...state, bookImage : action.payload
        }
      
        case 'CLEAR_NEWBOOK':
        return{
            ...state, newbook : action.payload
        }
        case 'UPDATE_BOOK_WITH_IMG':
        return{
            ...state,
            updateBook : action.payload.success,
            book:action.payload.doc
        }
        case 'UPDATE_BOOK_WITHOUT_IMG':
            return{
                ...state,
                updateBook : action.payload.success,
                book:action.payload.doc
            }
        case 'DELETE_BOOK':
        return{
            ...state,postDeleted : action.payload
        }
        case 'CLEAR_BOOK':
        return{
            ...state,
            updateBook : action.payload.updateBook,
            book : action.payload.book,
            postDeleted : action.payload.postDeleted
        }
        default:
            return state;
    }
}